// src/CartItem.jsx
import { useSelector, useDispatch } from 'react-redux';
import PropTypes from 'prop-types';
import { removeItem, updateQuantity } from './CartSlice.jsx';
import './CartItem.css';

function CartItem({ onContinueShopping }) {
  const dispatch = useDispatch();
  const cartItems = useSelector((state) => state.cart.cartItems);

  const calculateItemSubtotal = (item) => {
    return parseFloat(item.cost.substring(1)) * item.quantity;
  };

  const calculateTotalAmount = () => {
    return cartItems.reduce(
      (acc, item) => acc + calculateItemSubtotal(item),
      0
    );
  };

  const handleIncrement = (item) => {
    dispatch(updateQuantity({ name: item.name, quantity: item.quantity + 1 }));
  };

  const handleDecrement = (item) => {
    if (item.quantity > 1) {
      dispatch(updateQuantity({ name: item.name, quantity: item.quantity - 1 }));
    } else {
      dispatch(removeItem(item.name));
    }
  };

  const handleRemove = (item) => {
    dispatch(removeItem(item.name));
  };

  const handleCheckoutShopping = () => {
    alert('Functionality to be added for future reference');
  };

  return (
    <div className="cart-container">
      <h2>Your Cart</h2>
      {cartItems.length === 0 ? (
        <p>Your cart is empty.</p>
      ) : (
        <div className="cart-items">
          {cartItems.map((item, index) => (
            <div className="cart-item-card" key={index}>
              <img src={item.image} alt={item.name} />
              <div className="cart-item-details">
                <h3>{item.name}</h3>
                <p>{item.description}</p>
                <p><strong>Price:</strong> {item.cost}</p>
                <p><strong>Quantity:</strong> {item.quantity}</p>
                <p><strong>Subtotal:</strong> ${calculateItemSubtotal(item).toFixed(2)}</p>
                <button onClick={() => handleIncrement(item)}>+</button>
                <button onClick={() => handleDecrement(item)}>-</button>
                <button onClick={() => handleRemove(item)}>Remove</button>
              </div>
            </div>
          ))}
          <div className="cart-total">
            <h3>Total: ${calculateTotalAmount().toFixed(2)}</h3>
          </div>
        </div>
      )}
      <button className="continue-button" onClick={onContinueShopping}>
        Continue Shopping
      </button>
      <button className="checkout-button" onClick={handleCheckoutShopping}>
        Checkout
      </button>
    </div>
  );
}

CartItem.propTypes = {
  onContinueShopping: PropTypes.func.isRequired
};

export default CartItem;
